/* src/App.css */

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  overflow: hidden;
  perspective: 1px;
  overflow-x: hidden;


}

.parallax-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  /* Place the background behind other elements */
  transform-style: preserve-3d;
  backface-visibility: hidden;
  background: linear-gradient(rgba(255, 255, 255, 0.379), rgba(255, 255, 255, 0.379)), url('bg.jpg') center/cover fixed;
}

.parallax-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 100vh;
  overflow-y: auto;
}

textarea {
  border-color: black;
  border-width: 5px;
}

h1 {
  background-color: black;
  color: white;
  padding: 10px;
}

h3 {
  color: white;
}

.but {
  padding: 5px;
  margin: 5px;
  border-radius: 2px;
  border-color: black;
  border-width: 5px;
  background-color: purple;

}

.App {
  text-align: center;
  align-items: center;
  margin: 0;
  /* Remove default margin */
  padding: 0;
  /* Remove default padding */
  /*background-color: black;*/

  /* Do not repeat the image */
  font-family: 'Arial', sans-serif;
  /* Example font-family */

}

.text {
  text-align: center;
  margin: 20px;
  display: flex;
  margin-left: 50px;
}

.panel {

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 20px;
  width: 100%
}

.panel-container {

  display: flex;
  margin-bottom: 20px;
  flex-wrap: wrap;


}

.comic-display {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.comic-panel {
  background-color: black;
  /* Set your desired background color */
  padding: 5px;
  /* Set padding around the image */
  margin: 5px;
  /* Set margin around the image */
  border-radius: 3px;
}